//Bootstrap4 page
.pagination {
    display: -ms-flexbox;
    display: flex;
    padding-left: 0;
    list-style: none;
}
  
.page-link {
    position: relative;
    display: block;
    padding: 0.5rem 0.75rem;
    margin-left: -1px;
    line-height: 1.25;
    color: #000;
    background-color: #fff;
    border: 1px solid #000;
}
  
.page-link:hover {
    z-index: 2;
    color: $color-light;
    text-decoration: none;
    background-color: #000;
    border-color: #000;
}
  
.page-link:focus {
    z-index: 2;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.page-link:not(:disabled):not(.disabled) {
    cursor: pointer;
}

.page-item:first-child .page-link {
    margin-left: 0;
}

.page-item:last-child .page-link {
    background-color: #f2f2f2;
    &:hover{
        background-color: #000;
    }
}



.page-item.disabled .page-link {
    color: #6c757d;
    pointer-events: none;
    cursor: auto;
    background-color: #fff;
    border-color: #000;
}

.pagination-lg .page-link {
    padding: 9px 14px;
    font-size: 19px;
    line-height: 1.5;
    @include phone{
        padding: 9px;
    }
}

.pagination-lg .page-item:first-child .page-link {
    padding: 9px 11px;
    @include phone{
        padding: 9px;
    }
}
.pagination-lg .page-item:nth-child(2) .page-link {
    padding: 9px 15px;
    @include phone{
        padding: 9px 10px;
    }
}
.pagination-lg .page-item:last-child .page-link {
    padding: 9px 11px;
    @include phone{
        padding: 9px;
    }
}

.pagination-sm .page-link {
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    line-height: 1.5;
}